import React, {Component} from 'react';
import { Link } from "react-router-dom";
import logoscGray from '../images/footer-security-logo.png';



class Footer extends Component {

    render(){
        return (
                <div className="mastfoot">

                    <div className="h-bg-color-athens-gray  section">
                        <div className="row section__inner">
                            <div className="nav-footer  small-24  columns">
                                <div className="nav-footer__inner">
                                    <div className="row">
                                        <Link to="/" className="nav-footer__logo small-24 large-6 columns">
                                            <img src={logoscGray} alt={"Logo"}/>
                                        </Link>
                                        <div className="nav-footer__right-column medium-24 large-18  columns">
                                            <ul id="menu-mastfoot" className="nav-footer__menu h-float-left">
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/about">Packages</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/about">Equipment</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-custom menu-item-object-custom"><Link to="/about">Local</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/about">Resources</Link></li>
                                            </ul>
                                            <ul className="nav-footer__menu-legal">
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/terms">Terms &#038; Conditions</Link></li>
                                                <li className="nav-footer__menu-item menu-item menu-item-type-post_type menu-item-object-page"><Link to="/privacy">Privacy</Link></li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row section__inner">
                            <div className="columns" id="terms-conditions-start">
                                <p className="legal">Smart Home Control and Entertainment right at your fingertips. Control lighting, Audio/Video, Shades, HVAC, Security and more right from your iPad, at home or anywhere in the world. Now thats Smart! SmartTouch provides Home Automation, Home Audio, Whole House Audio, Home Control, Home Security, Home Theater, In wall speakers, Flush Mount Outets and Flush mount Key pads, Lighting Control and more. SmartTouch is an authorized dealer and installer for Basalte, Cineak, Crestron, Crestron Lighting, Crestron Shade and Drapery, Kaleidescape, Lexicon Pro, DynAudio, Meridian Audio, Artcoustic Speakers, Barco Projectors, Cinemaframe, Gray Loudspeakers, Marantz, Revel, Wisdom, Lutron Shades, Lutron Lighting Controls, James Loudspeakers, Screen Innovations, Seymour, Sony Projectors, Sony TVs and Savant. SmartTouch proudly serves Kolkata, Howrah, North 24 Parganas, South 24 Parganas, Bardhaman, Murshidabad, Hooghly, Cooch Behar, Darjeeling, South Dinajpur.</p>
                                <p className="legal">PYMOT Interactive Solutions Services, which help you manage your home environment and family lifestyle, requires the purchase and/or activation of an PYMOT alarm system with monitored burglary service and a compatible computer, cell phone or PDA with Internet and email access. These PYMOT Interactive Solutions Services do not cover the operation or maintenance of any household equipment/systems that are connected to the PYMOT Interactive Solutions Services/Equipment. All PYMOT Interactive Solutions Services are not available with the various levels of PYMOT Interactive Solutions Services. All PYMOT Interactive Solutions Services may not be available in all geographic areas. Standard message and data rates may apply to text alerts. You may be required to pay additional charges to purchase equipment required to utilize the PYMOT Interactive Solutions Services features you desire.</p>
                                <p className="legal">Installation starts at Rs 15000. Burglary, Fire, Carbon Monoxide and Medical Alert monitoring requires purchase and/or activation of an PYMOT security system with monitored Burglary, Fire, Carbon Monoxide and Medical Alert devices. Fire, Carbon Monoxide and Medical Alert services are an additional charge. Quality Service Plan (QSP) is PYMOTs Extended Limited Warranty. Including Quality Service Plan (QSP). Additional charges may apply in areas that require guard response service for municipal alarm verification. Prices subject to change. Prices may vary by market. Some insurance companies offer discounts on Homeowners Insurance. Please consult your insurance company. Local permit fees may be required. Satisfactory credit history required. Additional monitoring fees required for some services. Photos are for illustrative purposes only and may not reflect the exact product/service actually provided.</p>
                                <p className="legal">Actual system may vary from item pictured.</p>
                            </div>
                        </div>
                    </div>

                </div>
        )
    }
}
export default Footer;