import React, {Component} from 'react';
import { Link } from "react-router-dom";

import logoSc from '../images/logo-sc.svg';
import logoStreet from '../images/logo-ssusa.svg';
import iconcloseButton from '../images/icon-close-button.svg';
import arrowsubnav from '../images/arrow-sub-nav.svg';


class Footer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showdropMenu: false,
        };
        this.onHover = this.onHover.bind(this);
        this.onLeave = this.onLeave.bind(this);
        this.offmobNavigation = this.offmobNavigation.bind(this);
    }
    onHover() {
        const currentState = this.state.showdropMenu;
        this.setState({ showdropMenu: !currentState });
    }
    onLeave() {
        this.setState({ showdropMenu: false });
    }
    offmobNavigation() {
        document.getElementById("mobile-menu").classList.add("for-close-mob");
    }

    render(){
        return (

            <div>
                <nav className="nav nav-bg-color" role="navigation">
                    <div className="show-for-medium  row">
                        <div className="nav__links-section  small-24 medium-15 medium-offset-1 large-15 columns">
                            <Link to="/">
                                <img className="nav__securitychoice-logo hide-for-small-only" src={logoSc} alt={"logo"}/>
                                <img className="nav__logos show-for-small-only" src={logoSc} alt={"logo"}/>
                            </Link>
                            <div className="show-for-large">
                                <ul id="menu-masthead" className="nav__menu">
                                    <li className="nav__menu-item"><span title="About" className="nav__menu-item-link"><Link to="/about" onClick={this.onLeave}>About</Link></span></li>
                                    <li className="nav__menu-item"><span title="News" className="nav__menu-item-link"><Link to="/news" onClick={this.onLeave}>News</Link></span></li>
                                    <li className="nav__menu-item menu-item-has-children js-has-submenu">
                                        <button className="menu-link nav__menu-item-link main-menu-link" onClick={this.onHover} id='servicesNav'>
                                            <span>Services <span><img src={arrowsubnav} alt={"logo"}/></span></span>
                                        </button>
                                        <ul className={'sub-menu nav__submenu js-submenu hide-for-large'} style={{display: this.state.showdropMenu ? 'block' : 'none' }}>
                                            <li className="sub-menu-item"><Link to="/about" onClick={this.onLeave} title="Home Automation" className="nav__menu-item-link">Home Automation</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Enterprise Solutions" className="nav__menu-item-link">Enterprise Solutions</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Hotel Automation" className="nav__menu-item-link">Hotel Automation</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Hospital Solutions" className="nav__menu-item-link">Hospital Solutions</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Senior Living Automation" className="nav__menu-item-link">Senior Living Automation</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Warehouse Solutions" className="nav__menu-item-link">Warehouse Solutions</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Building Management System" className="nav__menu-item-link">Building Management System</Link></li>
                                            <li className="sub-menu-item"><Link to="/" onClick={this.onLeave} title="Energy Efficiency Solutions" className="nav__menu-item-link">Energy Efficiency Solutions</Link></li>
                                        </ul>
                                    </li>
                                    <li className="nav__menu-item"><span title="Contact" className="nav__menu-item-link"><Link to="/contact">Contact</Link></span></li>
                                </ul>
                            </div>
                        </div>
                        <div className="nav__logo-section  medium-8 columns">
                            <Link to="/">
                                <img className="nav__adt-safestreet" src={logoStreet} alt={"shield check icon"}/>
                            </Link>
                        </div>
                    </div>
                    <div className="nav__mobile  show-for-small-only">
                        <div className="row">
                            <div className="nav__logos  small-24 columns">
                                <Link to="/">
                                    <img className="nav__adt-sc" src={logoSc} alt={"logo"}/>
                                    <img className="nav__adt-safestreet" src={logoStreet} alt={"icon"}/>
                                </Link>
                            </div>
                        </div>
                    </div>
                </nav>

                <div>
                  <nav className={"mobile-nav reveal"} role="navigation" id="mobile-menu" data-reveal style={{display: this.props.mbmenu ? 'block' : 'none'}} >
                    <button className="modal-close-button  close-button  h-float-right" data-close aria-label="Close modal" type="button" onClick={this.offmobNavigation}>
                        <img aria-hidden="true" src={iconcloseButton} alt={"close button"}/>
                    </button>
                    <ul id="menu-mobile" className="mobile-nav__list-container">
                        <li className="mobile-nav__list-item"><Link to="/" onClick={this.offmobNavigation}>Home</Link></li>
                        <li className="mobile-nav__list-item"><Link to="/about" onClick={this.offmobNavigation}>About</Link></li>
                        <li className="mobile-nav__list-item"><Link to="/news" onClick={this.offmobNavigation}>News</Link></li>
                        <li className="mobile-nav__list-item"><Link to="">Services</Link>
                            <ul className="sub-menu nav__submenu js-submenu hide-for-large">
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Home Automation</Link></li>
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Enterprise Solutions</Link></li>
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Hotel Automation</Link></li>
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Hospital Solutions</Link></li>
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Warehouse Solutions</Link></li>
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Building Management System</Link></li>
                                <li className="menu-item"><Link to="" onClick={this.offmobNavigation}>Energy Efficiency Solutions</Link></li>
                            </ul>
                        </li>
                        <li className="mobile-nav__list-item"><Link to="/contact"  onClick={this.offmobNavigation}>Contact</Link></li>
                    </ul>
                  </nav>
                </div>

            </div>
        )
    }
}
export default Footer;