import React, {Component} from 'react';

import buildingmanagementSystem from "../images/news-image-1.jpg";

class ContactPage extends Component {

    render(){
        return (
            <div>
                <section className="regular slider" role="banner">
                    <div className="hero news-banner hero--secondary">
                        <div className="row">
                            <div className="small-24 large-14 columns  h-text-align-center">
                                <h1 className="hero__headline  h-margin-bottom-10">Recent News</h1>
                                <p className="hero__wrap-text">Help secure your home with top technology</p>
                                <button className="button button__primary  h-margin-bottom-30" data-open="free-quote-modal">
                                    Get a FREE Quote
                                </button>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="card  home-value-pts section">
                    <div className="row">
                        <div className="columns small-24">
                            <h2 className="card__section-header h-text-align-left h-text-color-blue">News</h2>
                        </div>
                    </div>
                    <div className="row align-center  js-slick-card-slider">
                        <div className="medium-24 large-16 small-24 column">
                            <div className="card__container js-card">
                                <div className="row h-text-align-left">
                                    <div className="medium-10 large-10 columns">
                                        <img src={buildingmanagementSystem} alt={"Building Management System"}/>
                                    </div>
                                    <div className="medium-14 large-14 small-24">
                                        <div className="card__content-container">
                                            <p className="card__content h-text-align-left h-font-size-20"><strong>Smart home solutions for the hard of hearing</strong></p>
                                            <p className="card__content h-text-align-left">
                                                For a communication-friendly environment, smart homes lead the way in providing safety, security, convenience and comfort. The Wirefree team have been included in the article, Creating a Communication Friendly Home from Connect Hearing, as experts in the field of wireless building automation.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className="medium-24 large-8 small-24 column">
                            <div className="card__container js-card">
                                <div className="card__content-container">
                                    <p className="card__header h-text-align-left">Recent News</p>
                                    <p className="card__content card__content--margin-top h-text-align-left">Pymot wants you to be completely comfortable with your automation system and are willing to give you a money back guarantee if you are not satisfied.</p>
                                    <p className="legal h-margin-top-10 h-text-align-left">Certain restrictions may apply.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
export default ContactPage;