import React, {Component} from 'react';

class ContactPage extends Component {

    render(){
        return (
            <div>
                <section className="regular slider" role="banner">
                    <div className="hero contactus-banner hero--secondary">
                        <div className="row">
                            <div className="small-24 large-14 columns  h-text-align-center">
                                <h1 className="hero__headline  h-margin-bottom-10">Contact Us</h1>
                                <p className="hero__wrap-text">Help secure your home with top technology</p>
                                <button className="button button__primary  h-margin-bottom-30" data-open="free-quote-modal">
                                    Get a FREE Quote
                                </button>
                            </div>
                        </div>
                    </div>
                </section>
                <section className="card  home-value-pts section">
                    <div className="row">
                        <div className="columns small-24">
                            <h2 className="card__section-header h-text-align-left h-text-color-blue">Contact Us</h2>
                        </div>
                    </div>
                    <div className="row align-center js-slick-card-slider">
                        <div className="medium-24 large-10 small-24 columns">
                            <div className="card__container js-card">
                                <div className="card__content-container">
                                    <article className="contact-address-col h-text-align-left">
                                        <div itemScope="">
                                            <div itemProp="address" itemScope="">
                                                <p>Contact us to know more about our Pymot range of automation solutions meant for your homes, communities, offices, hospitals, senior living, hotels and more. </p>
                                                <p className="address-txt"><strong>PYMOT PTE. LTD.</strong><br/>
                                                    <span itemProp="streetAddress">Newbarrapkpur Station, Kolkata  </span><br/>
                                                    <span itemProp="addressLocality">129/1 lenin sarani east kodalia new barrackpur kolkata</span><br/>
                                                    <span itemProp="addressRegion">Kolkata</span> - <span itemProp="postalCode">700110</span><br/>
                                                    Ph.+91-974-819-6580<br/>
                                                    Support: <a href="mailto:support@pymot.com">support@pymot.com</a>
                                                </p>
                                                <p className="address-txt"><strong>SALES OFFICE - 24 pgs, Kolkata, INDIA</strong><br/>
                                                    <span itemProp="streetAddress">Newbarrapkpur Station, Kolkata  </span><br/>
                                                    <span itemProp="addressLocality">129/1 lenin sarani east kodalia new barrackpur kolkata</span><br/>
                                                    <span itemProp="addressRegion">Kolkata</span> - <span itemProp="postalCode">700110, WestBengal</span><br/>
                                                    Support: <a href="mailto:support@pymot.com">support@pymot.com</a>
                                                </p>
                                            </div>
                                        </div>
                                        <div itemScope="">
                                            <p className="highlighted">Ph.: <strong><span itemProp="telephone">+91-974-819-6580</span></strong><br/>
                                                Sales: <a href="mailto:sales@pymot.com">sales@pymot.com</a>
                                            </p>
                                        </div>

                                        <div className="location-map-note">
                                            <p>Pymot Sales and Support Network:</p>
                                            {/*<ul>
                                                <li>
                                                    <strong>National</strong><br/>
                                                    Available in all major cities and <br/>
                                                    towns across the country
                                                </li>
                                                <li>
                                                    <strong>International</strong><br/>
                                                    Thailand | Sri Lanka | Middle East | USA
                                                </li>
                                            </ul>*/}
                                        </div>

                                    </article>
                                </div>
                            </div>
                        </div>
                        <div className="medium-24 large-14 small-24 columns">
                            <div className="card__container js-card">
                                <div className="contact-form-holder h-text-align-left">
                                    <p className="contact-title">For general inquiries,<br/> please use the following form</p>
                                    <p>Certain products are provided only in cities where we have Installation &amp; Support presence. We are adding new locations on a constant basis, so please let us know your location and requirements, and we will direct you to our nearest Channel Partners.</p>
                                    <div>
                                        <form method="post" action="">
                                                <input type="text" className="form-control ampfield mandatory form__input" placeholder="Name*" name="FirstName"/>
                                                <input type="text" className="form-control ampfield mandatory form__input" placeholder="Mobile Number*" name="MobileNumber"/>
                                                <input type="text" className="form-control ampfield mandatory form__input" placeholder="E-Mail*" name="Email"/>
                                                <input type="text" className="form-control ampfield form__input" placeholder="City*" name="City"/>
                                                <textarea className="form-control ampfield form__input__textarea" placeholder="Message*" name="Comments"></textarea>
                                                <button type="submit" className="button button__primary button--modal-form  h-margin-bottom-20 h-margin-top-20 h-max-width-none">
                                                    SUBMIT
                                                </button>

                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
export default ContactPage;