import React, {Component} from 'react';
import { Link } from "react-router-dom";
import Slider from "react-slick";
import iconPiggybank from "../images/icon-piggybank.svg";
import ourClientRel from "../images/our-client-01.jpg";
import ourClientjk from "../images/our-client-02.jpg";
import ourClientcol from "../images/our-client-03.jpg";
import ourClientacc from "../images/our-client-04.jpg";
import ourClientbha from "../images/our-client-05.jpg";
import ourClientwad from "../images/our-client-06.jpg";
import ourClientkal from "../images/our-client-07.jpg";
import ourClientmar from "../images/our-client-08.jpg";
import ourClienttra from "../images/our-client-09.jpg";

class AboutPage extends Component {
    render(){
        var packslider = {
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            dots: false,
            infinite: true,
            arrows: false,
            autoplaySpeed: 2000,

            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]


        };

        return (
            <div>
                <section className="regular slider" role="banner">
                    <div className="hero about-banner hero--secondary">
                        <div className="row">
                            <div className="small-24 large-14 columns  h-text-align-center">
                                <h1 className="hero__headline  h-margin-bottom-10">Advanced Automation Equipment and Features</h1>
                                <p className="hero__wrap-text">Help secure your home with top technology</p>
                                <button className="button button__primary  h-margin-bottom-30" data-open="free-quote-modal">
                                    Get a FREE Quote
                                </button>
                            </div>
                        </div>
                    </div>
                </section>
                <section className="card  home-value-pts section">
                    <div className="row">
                        <div className="columns small-24">
                            <h2 className="card__section-header h-text-align-left h-text-color-blue">About Us</h2>
                        </div>
                    </div>
                    <div className="row align-center  js-slick-card-slider">
                    <div className="medium-24 large-16 small-24 column">
                        <div className="card__container js-card">
                            <div className="card__content-container">
                                <p className="card__content h-text-align-left h-margin-bottom-10">
                                    You want the convenience of leading-edge, yet user-friendly technology for your home that adds comfort, convenience and perhaps a touch of luxury; technology custom designed to complement  and not compete with  your spaces.
                                </p>
                                <p className="card__content h-text-align-left h-margin-bottom-10">
                                    Such technology must let you command your spaces, while remaining behind the scenes, out of sight, though never out of mind. Above all, it must be incredibly easy to use and rock-solid reliable.
                                </p>
                                <p className="card__content h-text-align-left h-margin-bottom-10">
                                    Our business model comes from years of experience helping people to achieve their personal visions for home and office environments that work and play. We employ a corps of expert engineers, system designers and certified technician installers who team to deliver to you personally responsive, creative designs, system integration and flawless control of your spaces.
                                </p>
                                <p className="card__content h-text-align-left">
                                    Special care and attention to detail, when designing and creating your custom controlled spaces, is not an option; it is SmartTouchs one and only mission.
                                </p>

                            </div>
                            </div>
                        </div>
                        <div className="medium-24 large-8 small-24 column">
                            <div className="card__container js-card">
                                <div className="card__icon-container  home-value-pts__icon-container">
                                    <img className="home-value-pts__icon" src={iconPiggybank} alt={"piggybank savings icon"}/>
                                </div>
                                <div className="card__content-container">
                                    <h4 className="card__header">Money-Back <br/>Guarantee</h4>
                                    <p className="card__content card__content--margin-top">If youre dissatisfied with your PYMOT automation system and the company has tried its within six months.</p>
                                    <p className="legal h-margin-top-10">Certain restrictions may apply.</p>
                                </div>
                            </div>
                        </div>
                        </div>
                </section>
                <section className="banner banner--gafq  banner--adt-background">
                    <div className="row  columns">
                        <h2 className="banner__headline banner__headline--gafq">Help protect your home today.</h2>
                        <p className="banner__content  banner__content--gafq">One of our monitoring professionals can get you set up with a monitored home security system for you and your family.</p>
                        <button className="button button__primary" data-open="free-quote-modal">
                            Get a FREE Quote
                        </button>
                    </div>
                </section>
                <section className="section  h-padding-bottom-50">
                    <div className="section__inner">
                        <h2 className="h-margin-bottom-20 h-text-align-center h-text-color-blue">Our Clients</h2>
                        <p className="section__subhead mob-mb-10">We value customer service more than anything. We aim to make all our Ladue projects a white glove experience for our clients.</p>
                    </div>
                    <div className="card-package-container row js-slick-card-slider your-class" style={{display: 'block'}}>
                      <Slider {...packslider}>
                        <div className="card-package medium-24 large-24 small-24 column">
                            <div className="card-package__inner" data-height="match-height-container">
                                <div className="card-package__bottom" data-height="match-height-item">
                                        <img className="home-value-pts__icon" src={ourClientRel} alt={"Our Clients"}/>
                                </div>
                            </div>
                        </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                          <img className="home-value-pts__icon" src={ourClientjk} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                          <img className="home-value-pts__icon" src={ourClientcol} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>

                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                      <img className="home-value-pts__icon" src={ourClientacc} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                      <img className="home-value-pts__icon" src={ourClientbha} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                      <img className="home-value-pts__icon" src={ourClientwad} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                      <img className="home-value-pts__icon" src={ourClientkal} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                      <img className="home-value-pts__icon" src={ourClientmar} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>
                          <div className="card-package medium-24 large-24 small-24 column">
                              <div className="card-package__inner" data-height="match-height-container">
                                  <div className="card-package__bottom" data-height="match-height-item">
                                      <img className="home-value-pts__icon" src={ourClienttra} alt={"Our Clients"}/>
                                  </div>
                              </div>
                          </div>

                      </Slider>
                    </div>

                </section>
            </div>
        )
    }
}
export default AboutPage;